/*Breakpoint Intervalados*/
/*Breakpoint sem intervalos*/
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Reset DEFAULT CDZ */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

textarea {
  resize: none;
}

button::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner {
  border: none;
  padding: 0;
}

input[type=search] {
  -webkit-appearance: none;
}

button,
input[type=button],
input[type=reset],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: none;
}

a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: none;
}

input,
textarea,
select,
button {
  font: inherit;
  border-radius: 0;
}

/* Remove controls from Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]:hover,
input[type=number]:focus {
  -moz-appearance: textfield;
}

/* Remove controls from Safari and Chrome */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=text],
input[type=email],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

/* / Reset DEFAULT CDZ */
@font-face {
  font-family: "Font Name";
  src: url("../fonts/subset-FontName.eot");
  src: url("../fonts/subset-FontName.eot?#iefix") format("embedded-opentype"), url("../fonts/subset-FontName.woff2") format("woff2"), url("../fonts/subset-FontName.woff") format("woff"), url("../fonts/subset-FontName.ttf") format("truetype"), url("../fonts/subset-FontName.svg#FontName") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
body {
  font-family: "Font Name", sans-serif;
  font-size: 16px;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  border-radius: 0.25rem;
}

#app {
  position: relative;
  overflow: hidden;
}

.container {
  max-width: 1200px;
  padding: 0 1.5rem;
  margin: 0 auto;
  box-sizing: border-box;
}

.wrap-background-image {
  position: relative;
}
.wrap-background-image .background-image {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.wrap-background-image .background-image img {
  display: none;
}

.header {
  background: #000;
}
.header .header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.25rem;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  /*Responsivo*/
}
@media screen and (max-width: 479px) {
  .header .header-container {
    margin-right: 1.75rem;
  }
}
.header .header-container nav .header-menu {
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .header .header-container nav .header-menu {
    gap: 1.25rem;
  }
}
@media screen and (max-width: 479px) {
  .header .header-container nav .header-menu {
    gap: 0.75rem;
  }
}
.header .header-container nav .header-menu li a {
  display: inline-block;
  padding: 1rem 0;
  color: #fff;
  font-size: 1.125rem;
  border-radius: 0.25rem;
  position: relative;
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .header .header-container nav .header-menu li a {
    background: #111;
    padding: 0.75rem 1rem;
  }
}
@media screen and (max-width: 479px) {
  .header .header-container nav .header-menu li a {
    padding: 0.5rem 0.75rem;
    font-size: 0.875rem;
  }
}
.header .header-container nav .header-menu li a::after {
  content: "";
  display: block;
  width: 0;
  height: 0.125rem;
  margin-top: 0.25rem;
  background: #fff;
  transition: 0.3s;
  position: absolute;
}
.header .header-container nav .header-menu li a:hover::after {
  width: 100%;
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .header .header-container nav .header-menu li a:hover::after {
    display: none;
  }
}
.header .header-container nav .header-menu li a:hover {
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .header .header-container nav .header-menu li a:hover {
    background: #2e2e2e;
  }
}

.introduction {
  background: #000;
  box-shadow: inset 0 -7.5rem #fff;
}
@media screen and (max-width: 767px) {
  .introduction {
    background: #111;
    padding-top: 2.5rem;
    box-shadow: inset 0 -3.75rem #fff;
  }
}
.introduction .introduction-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.5rem;
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .introduction .introduction-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}
.introduction .introduction-container .content-introduction {
  align-self: end;
  padding-bottom: 12.5rem;
}
@media screen and (max-width: 767px) {
  .introduction .introduction-container .content-introduction {
    padding-bottom: 0rem;
  }
}
.introduction .introduction-container .content-introduction .title-introduction {
  margin-bottom: 2rem;
  font-size: 4rem;
  font-weight: bold;
  line-height: 1.125;
  color: #fff;
  /*RESPONSIVO*/
}
.introduction .introduction-container .content-introduction .title-introduction span {
  color: #ffbb00;
}
@media screen and (max-width: 1023px) {
  .introduction .introduction-container .content-introduction .title-introduction {
    font-size: 3rem;
  }
}
@media screen and (max-width: 767px) {
  .introduction .introduction-container .content-introduction .title-introduction {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}
.introduction .introduction-container .content-introduction .paragraph-introduction {
  margin-bottom: 1.25rem;
  font-size: 1.5rem;
  line-height: 1.5;
  color: #b2b2b2;
  /*RESPONSIVO*/
}
@media screen and (max-width: 1023px) {
  .introduction .introduction-container .content-introduction .paragraph-introduction {
    font-size: 1.125rem;
  }
}
.introduction .introduction-container .content-introduction button {
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .introduction .introduction-container .content-introduction button {
    padding: 0.75rem 1rem;
  }
}
.introduction .introduction-container .content-introduction button a {
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .introduction .introduction-container .content-introduction button a {
    font-size: 1rem;
  }
}
.introduction .introduction-container .photo-introduction {
  margin-bottom: -7.5rem;
}
.introduction .introduction-container .photo-introduction img {
  height: 100%;
  object-fit: cover;
  /*RESPONSIVO*/
}
@media screen and (max-width: 767px) {
  .introduction .introduction-container .photo-introduction img {
    height: 18.75rem;
    width: 100%;
  }
}

.button {
  padding: 1rem 2rem;
  background: linear-gradient(#ffbf00, #f2a50c);
  border-radius: 0.25rem;
  box-shadow: 0 0.0625rem 0.125rem rgba(0, 0, 0, 0.1);
  border: none;
  text-transform: uppercase;
}
.button a {
  color: #332200;
  font-size: 1.125rem;
  line-height: 1.33;
  font-weight: bold;
}

.button:hover {
  background: linear-gradient(#ffb60d, #e59317);
}